      if (changeFields.length) {
        const { error } = await custom.updateArmgroupMLValidation(changeFields);
        if (error) {
          this.$alert.error(error);
          return;
        }
      }
      if (newFields.length) {
        const { error } = await custom.insertArmgroupMLValidation(newFields);
        if (error) {
          this.$alert.error(error);
          return;
        }
      }

      this.getDataValidation();
