<template>
  <div id="custom-form-view" :style="{ width: formWidth }">
    <!-- form meta -->

    <div class="header">
      <div class="col-auto logo">
        <img :src="_logo" />
      </div>
      <div class="col q-mr-xl text-center">
        <div class="title-1">Request Submission</div>
        <div class="description">To submit the Claim Request</div>
      </div>

      <!-- <BaseActionButton
        is-flat
        :icon="isMaximized ? 'eva-collapse' : 'eva-expand'"
        @click="toggleWidth"
      />
      <ThemeSwitcher class="q-ml-sm" :no-border="false" /> -->
    </div>

    <!-- ... -->

    <!-- form -->
    <div class="content">
      <FormFieldLabel label="Claimant Search" />

      <div class="content-wrapper">
        <div v-if="$q.screen.width > 1280" class="row q-mb-md">
          <!-- lender -->
          <MultiSelectField
            v-model="lender"
            label="lender"
            :options="lenderList"
            class="col"
          />
          <!-- ... -->

          <!-- first name -->

          <TextField
            v-model="firstName"
            label="first name"
            class="col q-ml-md"
            @enter="getData"
          />

          <!-- ... -->

          <!-- last name -->

          <TextField
            v-model="lastName"
            label="last name"
            class="col q-ml-md"
            @enter="getData"
          />

          <!-- ... -->

          <!-- dob -->

          <DateField
            v-model="dob"
            label="DOB"
            class="col q-ml-md"
            @enter="getData"
          />

          <!-- ... -->

          <!-- province -->

          <TextField
            v-model="province"
            label="province"
            class="col q-ml-md"
            @enter="getData"
          />

          <!-- ... -->

          <!-- claim number -->

          <TextField
            v-model="claimNumber"
            label="claim number"
            class="col q-ml-md"
            @enter="getData"
          />

          <!-- ... -->

          <!-- contract number -->

          <TextField
            v-model="contractNumber"
            label="contract number"
            class="col q-ml-md"
            @enter="getData"
          />

          <!-- ... -->

          <!-- ref number -->

          <TextField
            v-model="refNumber"
            label="Ref. number"
            class="col q-ml-md"
            @enter="getData"
          />

          <!-- ... -->

          <!-- search -->
          <BaseButton
            label="Search"
            icon="eva-search-outline"
            class="searchBtn col-auto q-ml-md"
            @click="getData"
          />
          <!-- ... -->
        </div>
        <div v-else>
          <div class="row q-mb-md">
            <!-- lender -->
            <MultiSelectField
              v-model="lender"
              label="lender"
              :options="lenderList"
              class="col"
            />
            <!-- ... -->

            <!-- first name -->

            <TextField
              v-model="firstName"
              label="first name"
              class="col q-ml-md"
              @enter="getData"
            />

            <!-- ... -->

            <!-- last name -->

            <TextField
              v-model="lastName"
              label="last name"
              class="col q-ml-md"
              @enter="getData"
            />

            <!-- ... -->

            <!-- dob -->

            <DateField
              v-model="dob"
              label="DOB"
              class="col q-ml-md"
              @enter="getData"
            />

            <!-- ... -->
          </div>
          <div class="row q-mb-md">
            <!-- province -->

            <TextField
              v-model="province"
              label="province"
              class="col"
              @enter="getData"
            />

            <!-- ... -->

            <!-- claim number -->

            <TextField
              v-model="claimNumber"
              label="claim number"
              class="col q-ml-md"
              @enter="getData"
            />

            <!-- ... -->

            <!-- contract number -->

            <TextField
              v-model="contractNumber"
              label="contract number"
              class="col q-ml-md"
              @enter="getData"
            />

            <!-- ... -->

            <!-- ref number -->

            <TextField
              v-model="refNumber"
              label="Ref. number"
              class="col q-ml-md"
              @enter="getData"
            />

            <!-- ... -->

            <!-- search -->
            <BaseButton
              label="Search"
              icon="eva-search-outline"
              class="searchBtn col-auto q-ml-md"
              @click="getData"
            />
          </div>
        </div>
      </div>
      <div class="row q-mb-xs">
        <div class="col q-mt-sm">
          <FormFieldLabel label="Search Results" />
        </div>
        <q-space />
        <div class="col-auto">
          <ExpandableSearch class="q-mr-sm" @search="searchRow" />
        </div>
        <div class="col-auto">
          <BaseButton
            label="Add Contract and Send Package"
            icon="eva-plus-outline"
            color="gray"
            @click="addContractSheet = true"
          />
        </div>
      </div>
      <div class="content-wrapper tables">
        <BaseScrollbar height="calc(100vh - 360px)">
          <div class="row">
            <FormFieldLabel :label="contractCount" />
            <BaseActionButton
              :icon="contractExpand ? 'mdi-chevron-down' : 'mdi-chevron-up'"
              class="q-ml-sm small"
              is-flat
              no-border
              @click="contractExpand = !contractExpand"
            />
          </div>
          <table v-if="contractExpand">
            <thead>
              <tr>
                <!-- info -->

                <th class="sticky" style="left: 0; width: 15px"></th>

                <!-- ... -->
                <!-- columns -->

                <th v-for="column in contractColumnsFilter" :key="column.id">
                  {{ column.label }}
                </th>

                <!-- ... -->

                <!-- actions -->

                <th class="sticky"></th>

                <!-- ... -->
              </tr>
            </thead>
            <tbody>
              <template v-for="item in tableContracts">
                <tr :key="item.id">
                  <!-- info -->

                  <td class="sticky" style="left: 0; width: 15px">
                    <div class="row items-center justify-center">
                      <BaseButton
                        label="Expand"
                        :icon="item.infoVisible ? 'eva-eye-off' : 'eva-eye'"
                        color="secondary"
                        is-flat
                        no-border
                        @click="item.infoVisible = !item.infoVisible"
                      />
                    </div>
                  </td>

                  <!-- ... -->

                  <!-- columns -->

                  <td v-for="column in contractColumnsFilter" :key="column.id">
                    <template v-if="column.editable">
                      <template v-if="column.valueType === 'email'">
                        <ValidationProvider
                          v-slot="{ errors }"
                          :name="column.name"
                          :rules="{ required: column.required, email: true }"
                        >
                          <TextField
                            v-model="item[column.name]"
                            is-mandatory
                            :error="errors[0]"
                          />
                        </ValidationProvider>
                      </template>
                      <template v-else-if="column.name === 'lender'">
                        <ValidationProvider
                          v-slot="{ errors }"
                          :name="column.name"
                          :rules="{ required: column.required }"
                        >
                          <SelectField
                            v-model="item[column.name]"
                            is-mandatory
                            :options="lenderList"
                            :error="errors[0]"
                          />
                        </ValidationProvider>
                      </template>
                      <template v-else>
                        <TextField v-model="item[column.name]" is-mandatory />
                      </template>
                    </template>
                    <template v-else>
                      <slot
                        :name="column.name"
                        :value="item[column.name]"
                        :item="item"
                      >
                        {{ item[column.name] }}
                      </slot>
                    </template>
                  </td>

                  <!-- ... -->

                  <!-- actions -->

                  <td class="sticky">
                    <div class="row justify-center">
                      <BaseButton
                        label="Send Package"
                        icon="mdi-file-send-outline"
                        class="col-auto q-ml-md"
                        color="secondary"
                        @click="getProduct(item.id)"
                      />
                    </div>
                  </td>

                  <!-- ... -->
                </tr>
                <tr v-if="item.infoVisible" :key="'sub-' + item.id">
                  <td :colspan="contractColumnsFilter.length + 2">
                    <table class="subDetails">
                      <thead>
                        <th
                          v-for="column in contractColumnsInfo"
                          :key="column.id"
                        >
                          {{ column.label }}
                        </th>
                      </thead>
                      <tbody>
                        <td
                          v-for="column in contractColumnsInfo"
                          :key="column.id"
                        >
                          <slot
                            :name="column.name"
                            :value="item[column.name]"
                            :item="item"
                          >
                            {{ item[column.name] }}
                          </slot>
                        </td>
                      </tbody>
                    </table>
                  </td>
                </tr>
              </template>
            </tbody>
          </table>
          <div class="row">
            <FormFieldLabel :label="claimCount" />
            <BaseActionButton
              :icon="claimExpand ? 'mdi-chevron-down' : 'mdi-chevron-up'"
              class="q-ml-sm small"
              is-flat
              no-border
              @click="claimExpand = !claimExpand"
            />
          </div>
          <table v-if="claimExpand">
            <thead>
              <tr>
                <!-- info -->

                <th class="sticky" style="left: 0; width: 15px"></th>
                <!-- ... -->

                <!-- columns -->

                <th v-for="column in claimColumnsFilter" :key="column.id">
                  {{ column.label }}
                </th>

                <!-- ... -->

                <!-- actions -->

                <th class="sticky"></th>

                <!-- ... -->
              </tr>
            </thead>
            <tbody>
              <template v-for="item in tableClaims">
                <tr :key="item.id">
                  <!-- info -->

                  <td class="sticky" style="left: 0; width: 15px">
                    <div class="row items-center justify-center">
                      <BaseButton
                        label="Expand"
                        :icon="item.infoVisible ? 'eva-eye-off' : 'eva-eye'"
                        color="secondary"
                        is-flat
                        no-border
                        @click="item.infoVisible = !item.infoVisible"
                      />
                    </div>
                  </td>

                  <!-- ... -->

                  <!-- columns -->

                  <td v-for="column in claimColumnsFilter" :key="column.id">
                    <template v-if="column.name === 'history'">
                      <BaseActionButton
                        v-tooltip.bottom="'Claim History'"
                        icon="mdi-history"
                        color="secondary"
                        is-flat
                        no-border
                        @click="claimHistory(item)"
                      />
                    </template>
                    <template v-else-if="column.editable">
                      <template v-if="column.valueType === 'email'">
                        <ValidationProvider
                          v-slot="{ errors }"
                          :name="column.name"
                          :rules="{ required: column.required, email: true }"
                        >
                          <TextField
                            v-model="item[column.name]"
                            is-mandatory
                            :error="errors[0]"
                          />
                        </ValidationProvider>
                      </template>
                      <template v-else>
                        <TextField v-model="item[column.name]" is-mandatory />
                      </template>
                    </template>
                    <template v-else>
                      <slot
                        :name="column.name"
                        :value="item[column.name]"
                        :item="item"
                      >
                        {{ item[column.name] }}
                      </slot>
                    </template>
                  </td>

                  <!-- ... -->

                  <!-- actions -->

                  <td class="sticky">
                    <div class="row justify-center q-mt-sm">
                      <template v-if="claimStatus(item)">
                        <div @click="reopenClaim(item.id)">
                          <q-chip outline color="pink" class="chips pink-hover">
                            <q-avatar
                              icon="mdi-arrow-top-right"
                              class="q-mr-sm"
                              color="pink"
                              text-color="white"
                            />
                            Re-open
                          </q-chip>
                        </div>
                      </template>
                      <!-- <template v-else>
                        <q-chip outline color="gray" class="chips gray-hover">
                          <q-avatar
                            icon="mdi-plus"
                            class="q-mr-sm"
                            color="gray"
                            text-color="white"
                          />
                          Continue
                        </q-chip>
                      </template> -->
                      <!-- <div @click="newClaim(item.id)">
                        <q-chip
                          outline
                          color="orange"
                          class="chips orange-hover"
                          @click="newClaim(item.id)"
                        >
                          <q-avatar
                            icon="mdi-plus"
                            class="q-mr-sm"
                            color="orange"
                            text-color="white"
                          />
                          New Claim
                        </q-chip>
                      </div> -->
                      <div @click="viewClaim(item.id)">
                        <q-chip
                          outline
                          color="orange"
                          class="chips orange-hover"
                        >
                          <q-avatar
                            icon="eva-eye-outline"
                            class="q-mr-sm"
                            color="orange"
                            text-color="white"
                          />
                          View Claim
                        </q-chip>
                      </div>
                    </div>
                  </td>

                  <!-- ... -->
                </tr>
                <tr v-if="item.infoVisible" :key="'sub-' + item.id">
                  <td :colspan="claimColumnsFilter.length + 2">
                    <table class="subDetails">
                      <thead>
                        <th v-for="column in claimColumnsInfo" :key="column.id">
                          {{ column.label }}
                        </th>
                      </thead>
                      <tbody>
                        <td v-for="column in claimColumnsInfo" :key="column.id">
                          <slot
                            :name="column.name"
                            :value="item[column.name]"
                            :item="item"
                          >
                            {{ item[column.name] }}
                          </slot>
                        </td>
                      </tbody>
                    </table>
                  </td>
                </tr>
              </template>
            </tbody>
          </table>
        </BaseScrollbar>
      </div>
    </div>
    <!-- ... -->

    <!-- footer -->

    <!-- <div class="footer">
      <div class="label">Powered by</div>
      <img src="@/assets/logo/logo-light.png" alt="logo" />
    </div> -->

    <!-- ... -->

    <AddContract
      :value="addContractSheet"
      :lender-list="lenderList"
      @initiate="processInitiate"
      @close="addContractSheet = false"
    ></AddContract>

    <Modal
      v-model="productModal"
      width="40vw"
      height="60vh"
      has-footer
      @input="productModal = false"
    >
      <!-- title -->

      <template #title> Choose Product</template>

      <!-- ... -->

      <template #default>
        <div id="productModal">
          <!-- product -->

          <MultiSelectField
            v-model="products"
            label="product"
            is-mandatory
            :options="productList"
            class="field"
          />

          <!-- ... -->
        </div>
      </template>

      <template #footer>
        <BaseButton
          label="Send Package"
          color="primary"
          @click="processInitiate('')"
        />
      </template>
    </Modal>
    <Modal
      v-model="claimHistoryModal"
      width="70vw"
      @input="claimHistoryModal = false"
    >
      <!-- title -->

      <template #title>Claim History</template>

      <!-- ... -->

      <template #default>
        <div id="claimHistory">
          <table>
            <thead>
              <tr>
                <!-- columns -->

                <th v-for="column in historyColumns" :key="column.id">
                  {{ column.label }}
                </th>

                <!-- ... -->
              </tr>
            </thead>

            <tbody>
              <tr v-for="item in claimHistoryData" :key="item.id">
                <!-- columns -->

                <td v-for="column in historyColumns" :key="column.id">
                  <slot
                    :name="column.name"
                    :value="item[column.name]"
                    :item="item"
                  >
                    {{ item[column.name] || "-" }}
                  </slot>
                </td>

                <!-- ... -->
              </tr>
            </tbody>
          </table>
        </div>
      </template>
    </Modal>

    <Sheet
      v-model="processSheet"
      width="100vw"
      no-padding
      has-footer
      @input="processSheet = false"
    >
      <template #title> {{ headerTitle }} </template>

      <div id="content-wrapper" class="row">
        <div class="col">
          <CustomProcessForm
            :form-id="formId"
            :panels="panels"
            :secondary-panels="secondaryPanels"
            :selected-process="selectedProcess"
            :is-readonly="false"
            :check-list.sync="checkList"
            :form-model="formModel"
            :form-fields="formFields"
            :inbox="true"
          />
        </div>
        <div class="col-auto q-mr-sm leftBorder">
          <template v-for="tab in tabs">
            <BaseActionButton
              :key="tab.id"
              v-tooltip.left="tab.label"
              is-flat
              no-border
              :icon="tab.icon"
              color="primary"
              class="tabButtons"
              @click="showLinks(tab.value)"
            >
              <span v-if="tab.count" class="badge">{{ tab.count }} </span>
            </BaseActionButton>
          </template>
        </div>
      </div>
      <!-- footer -->
      <template #footer>
        <div class="footer row">
          <BaseButton
            label="Update Claim Details"
            class="q-ml-md"
            color="secondary"
            @click="updateClaimDetails()"
          />
        </div>
      </template>
    </Sheet>

    <Sheet
      v-model="attachmentSheet"
      width="30vw"
      :sub-actions="false"
      no-padding
      @input="attachmentSheet = false"
    >
      <!-- title -->

      <template #title> Attachments </template>

      <template #actions>
        <BaseActionButton
          v-if="selectedProcess.processId"
          v-tooltip.top="'refresh'"
          icon="mdi-refresh"
          color="secondary"
          no-border
          is-flat
          size="20px"
          class="q-mr-sm"
          @click="getAttachments()"
        />
      </template>

      <!-- content -->
      <div id="content-wrapper">
        <BaseScrollbar>
          <template v-for="row in checkList">
            <template v-if="row.name">
              <div v-if="!row.attach" :key="row.uid" class="checkList">
                <div class="row">
                  <div class="col-auto">
                    <BaseIcon name="mdi-file-check-outline" color="primary" />
                  </div>
                  <div class="col q-ml-sm">
                    <div class="row">
                      <div class="col-12">
                        {{ row.name }}
                      </div>
                      <div v-if="row.required" class="col-12 q-mt-sm">
                        <q-chip outline color="red" class="chips small">
                          Required
                        </q-chip>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div v-else :key="row.uid">
                <template v-for="file in attachmentFilterCheckList(row.name)">
                  <div :key="file.id" class="attachmentList">
                    <div class="title row">
                      <div v-if="selectedProcess.processId" class="col-auto">
                        <!-- <CheckboxField v-model="file.checked" class="col-auto" /> -->
                        <template v-if="file.loading">
                          <BaseIcon
                            name="mdi-loading"
                            color="secondary"
                            class="q-mr-sm loading"
                          />
                        </template>
                        <template v-else>
                          <BaseIcon
                            :name="
                              file.checked
                                ? 'eva-checkmark-circle-2'
                                : 'eva-radio-button-off-outline'
                            "
                            color="secondary"
                            class="q-mr-sm"
                            @click="file.checked = !file.checked"
                          />
                        </template>
                      </div>
                      <div v-else>
                        <template v-if="file.loading">
                          <BaseIcon
                            name="mdi-loading"
                            color="secondary"
                            class="q-mr-sm loading"
                          />
                        </template>
                      </div>
                      <div v-if="file.formJsonId" class="col-auto">
                        <BaseIcon
                          v-tooltip:green.top="getLabel(file.formJsonId)"
                          name="mdi-form-select"
                          color="primary"
                          class="q-mr-sm"
                        />
                      </div>
                      <div class="col-auto">
                        <FileIcon
                          :mime-type="fileType(file.name)"
                          class="mini-avatar q-mr-sm"
                        />
                      </div>
                      <div class="col ellipsis" @click="openFile(file)">
                        {{ file.name }}
                      </div>
                      <div v-if="!file.loading" class="col-auto">
                        <BaseActionButton
                          v-tooltip.top="'Download'"
                          icon="eva-download-outline"
                          color="primary"
                          no-border
                          is-flat
                          size="20px"
                          @click.stop="fileMenuAction(file, 'Download')"
                        />
                      </div>
                      <div v-if="!file.loading" class="col-auto">
                        <BaseActionButton
                          v-tooltip.top="'Print'"
                          icon="eva-printer-outline"
                          color="primary"
                          no-border
                          is-flat
                          size="20px"
                          @click.stop="fileMenuAction(file, 'Print')"
                        />
                      </div>
                    </div>

                    <div class="meta row">
                      <div class="col row">
                        <!-- created at -->

                        <div>{{ $day.format(file.createdAt) }}</div>

                        <!-- ... -->

                        <!-- created by -->

                        <template v-if="file.createdBy">
                          <BaseIcon name="mdi-circle-small" />
                          <div>{{ file.createdByEmail }}</div>
                        </template>

                        <!-- ... -->

                        <!-- size -->

                        <template v-if="file.size">
                          <BaseIcon name="mdi-circle-small" />
                          <div>{{ fileSize(file.size) }}</div>
                        </template>

                        <!-- ... -->
                      </div>
                    </div>
                  </div>
                </template>
              </div>
            </template>
          </template>
          <template v-for="file in attachmentList">
            <template v-if="isCheckList(file.name)">
              <div :key="file.id" class="attachmentList">
                <div class="title row">
                  <template v-if="file.loading">
                    <BaseIcon
                      name="mdi-loading"
                      color="secondary"
                      class="q-mr-sm loading"
                    />
                  </template>
                  <div v-if="selectedProcess.processId" class="col-auto">
                    <!-- <CheckboxField v-model="file.checked" class="col-auto" /> -->

                    <BaseIcon
                      :name="
                        file.checked
                          ? 'eva-checkmark-circle-2'
                          : 'eva-radio-button-off-outline'
                      "
                      color="secondary"
                      class="q-mr-sm"
                      @click="file.checked = !file.checked"
                    />
                  </div>
                  <div v-if="file.formJsonId" class="col-auto">
                    <BaseIcon
                      v-tooltip:green.top="getLabel(file.formJsonId)"
                      name="mdi-form-select"
                      color="primary"
                      class="q-mr-sm"
                    />
                  </div>
                  <div class="col-auto">
                    <FileIcon
                      :mime-type="fileType(file.name)"
                      class="mini-avatar q-mr-sm"
                    />
                  </div>
                  <div class="col ellipsis" @click="openFile(file)">
                    {{ file.name }}
                  </div>
                  <div v-if="!file.loading" class="col-auto">
                    <BaseActionButton
                      v-tooltip.top="'Download'"
                      icon="eva-download-outline"
                      color="primary"
                      no-border
                      is-flat
                      size="20px"
                      @click.stop="fileMenuAction(file, 'Download')"
                    />
                  </div>
                  <div v-if="!file.loading" class="col-auto">
                    <BaseActionButton
                      v-tooltip.top="'Print'"
                      icon="eva-printer-outline"
                      color="primary"
                      no-border
                      is-flat
                      size="20px"
                      @click.stop="fileMenuAction(file, 'Print')"
                    />
                  </div>
                </div>

                <div class="meta row">
                  <div class="col row">
                    <!-- created at -->

                    <div>{{ $day.format(file.createdAt) }}</div>

                    <!-- ... -->

                    <!-- created by -->

                    <template v-if="file.createdBy">
                      <BaseIcon name="mdi-circle-small" />
                      <div>{{ file.createdByEmail }}</div>
                    </template>

                    <!-- ... -->

                    <!-- size -->

                    <template v-if="file.size">
                      <BaseIcon name="mdi-circle-small" />
                      <div>{{ fileSize(file.size) }}</div>
                    </template>

                    <!-- ... -->
                  </div>
                </div>
              </div>
            </template>
          </template>
        </BaseScrollbar>
      </div>
    </Sheet>
    <Sheet
      v-model="commentsSheet"
      width="30vw"
      height="80vh"
      no-padding
      @input="commentsSheet = false"
    >
      <!-- title -->

      <template #title> Comments </template>

      <!-- ... -->

      <template #actions>
        <BaseActionButton
          v-tooltip.top="'refresh'"
          icon="mdi-refresh"
          color="secondary"
          no-border
          is-flat
          size="20px"
          class="q-mr-sm"
          @click="getComments(0)"
        />
      </template>

      <!-- content -->
      <div id="content-wrapper">
        <BaseScrollbar height="calc(100vh - 160px)">
          <!-- comments -->
