              </div>
            </template>
            <div class="chip" @click="showFullTypeList">
              <div>
                {{ showAllButtonText }}
                <BaseIcon
                  name="mdi-menu-down"
                  :class="{ 'rotate-180': isShowAll }"
                />
              </div>
            </div>
          </div>

          <div
            class="row items-center q-py-md"
            style="border-bottom: 1px solid var(--divider-color)"
          >
